import React from 'react';
import {Button, Form} from "react-bootstrap";
import OwnerSearchModal from "../../common/OwnerSearchModal";
import {ButtonBox, DefaultSearchBarLayout, RightHorizontal} from "../../../util/CommonStyledComponents";

const VOrderSearchCondition = ({
                                   search, init, onClick, onChange,
                                   showOwnerModal, setShowOwnerModal,
                                   selectOwnerModal
                               }) => {

    return (
        <DefaultSearchBarLayout>
            <OwnerSearchModal show={showOwnerModal} onClose={() => setShowOwnerModal(false)}
                              onSelect={selectOwnerModal}/>

            <Form.Group>
                <Form.Label> 
                    <Button style={{marginLeft: "90px", paddingTop: "0px", paddingBottom: "0px"}} size={"sm"}
                            variant={"outline-info"}
                            onClick={() => setShowOwnerModal(true)}></Button>
                </Form.Label>
                <Form.Control id={"ownerId"} type={"number"} placeholder=""
                              value={search?.ownerId ?? ""}
                              onChange={(event) => onChange(event)}
                />
            </Form.Group>
            <RightHorizontal/>
            <Form.Group>
                <Form.Label></Form.Label>
                <Form.Control id={"orderNo"} type={"text"} placeholder="" value={search.orderNo}
                              onChange={(event) => onChange(event)}/>
            </Form.Group>
            <RightHorizontal/>

            <Form.Group>
                <Form.Label type="text"></Form.Label>
                <Form.Control as="select" id="orderStatus" value={search.orderStatus}
                              onChange={(event) => onChange(event)}>
                    <option value=""></option>
                    <option value="ORDER"></option>
                    <option value="ORDER_CONFIRM"></option>
                    <option value="DELIVERY"></option>
                    <option value="DELIVERY_CONFIRM"></option>
                    <option value="CANCEL"></option>
                </Form.Control>
            </Form.Group>
            <RightHorizontal/>

            <Form.Group>
                <Form.Label></Form.Label>
                <Form.Control id={"startAt"} type={"dateTime-local"} placeholder="" value={search.startAt}
                              onChange={(event) => onChange(event)}/>
            </Form.Group>
            <RightHorizontal/>

            <Form.Group>
                <Form.Label></Form.Label>
                <Form.Control id={"endAt"} type={"dateTime-local"} placeholder="" value={search.endAt}
                              onChange={(event) => onChange(event)}/>
            </Form.Group>
            <RightHorizontal/>
            <ButtonBox>
                <Button variant="outline-danger" onClick={init}></Button>
            </ButtonBox>
            <RightHorizontal/>
            <ButtonBox>
                <Button variant="outline-success" onClick={onClick}></Button>
            </ButtonBox>
        </DefaultSearchBarLayout>
    )
}

export default VOrderSearchCondition;