import React from 'react';
import {Button, Form} from "react-bootstrap";
import {ButtonBox, DefaultSearchBarLayout, RightHorizontal} from "../../../util/CommonStyledComponents";

const VSettleSearchCondition = ({search, setSearch, onChange, fetchPageApi, setOwnerShow}) => {
    return (
        <DefaultSearchBarLayout>
            <Form.Group>
                <Form.Label> 
                    <Button style={{marginLeft: "90px", paddingTop: "0px", paddingBottom: "0px"}}
                            size={"sm"} variant={"outline-info"}
                            onClick={() => setOwnerShow(true)}></Button>
                </Form.Label>
                <Form.Control id={"ownerId"} type={"number"} placeholder=""
                              value={search?.ownerId ?? ""}
                              onChange={(event) => onChange(event)}/>
            </Form.Group>
            <RightHorizontal/>

            <Form.Group>
                <Form.Label> </Form.Label>
                <Form.Control id={"settleStatus"} as="select" placeholder=""
                              value={search?.settleStatus ?? ""}
                              onChange={(event) => onChange(event)}>
                    <option value=""></option>
                    <option value="COMPLETED"></option>
                    <option value="CREATED"></option>
                </Form.Control>
            </Form.Group>
            <RightHorizontal/>

            <Form.Group>
                <Form.Label> </Form.Label>
                <Form.Control id={"settleType"} as="select" placeholder=""
                              value={search?.settleType ?? ""}
                              onChange={(event) => onChange(event)}>
                    <option value=""></option>
                    <option value="DAILY"></option>
                    <option value="WEEK"></option>
                    <option value="MONTH"></option>
                </Form.Control>
            </Form.Group>
            <RightHorizontal/>

            <Form.Group>
                <Form.Label></Form.Label>
                <Form.Control id={"startAt"} type={"date"} placeholder=""
                              value={search?.startAt ?? ""}
                              onChange={(event) => onChange(event)}/>
            </Form.Group>
            <RightHorizontal/>
            <Form.Group>
                <Form.Label></Form.Label>
                <Form.Control id={"endAt"} type={"date"} placeholder=""
                              value={search?.endAt ?? ""}
                              onChange={(event) => onChange(event)}/>
            </Form.Group>
            <RightHorizontal/>

            <ButtonBox>
                <Button variant="outline-danger" onClick={() => setSearch({startAt: "", endAt: "",})}></Button>
            </ButtonBox>
            <RightHorizontal/>
            <ButtonBox>
                <Button variant="outline-success" onClick={() => fetchPageApi()}></Button>
            </ButtonBox>
        </DefaultSearchBarLayout>

    )
}

export default VSettleSearchCondition;