import React from 'react';
import {Button, Form, Modal} from "react-bootstrap";
import {RightHorizontal} from "../../../util/CommonStyledComponents";

const VSettleRegisterForm = ({show, close, onChange, register, registerApi}) => {
    return (
        <Modal show={show} onHide={close}>
            <Modal.Header closeButton>
                <Modal.Title> </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                       
                <br/>
                    .
                <br/><br/>
                <Form.Group>
                    <Form.Label> </Form.Label>
                    <Form.Control id={"ownerId"} type={"number"} placeholder=""
                                  onChange={onChange}/>
                </Form.Group>

                <Form.Group>
                    <Form.Label> </Form.Label>
                    <Form.Control id={"settleType"} as="select" placeholder=""
                                  onChange={onChange}>
                        <option value=""></option>
                        <option value="DAILY"></option>
                        <option value="WEEK"></option>
                        <option value="MONTH"></option>
                    </Form.Control>
                </Form.Group>

                {register.settleType === "DAILY" &&
                <Form.Group>
                    <Form.Label> .</Form.Label>
                    <Form.Control id={"criteriaDate"} type={"date"} placeholder=""
                                  onChange={onChange}/>
                </Form.Group>
                }

                {register.settleType === "WEEK" &&
                <Form.Group>
                    <Form.Group>
                        <Form.Label>  ,   .</Form.Label>
                        <Form.Control id={"criteriaDate"} type={"date"} placeholder=""
                                      onChange={onChange}/>
                    </Form.Group>
                </Form.Group>
                }

                {register.settleType === "MONTH" &&
                <Form.Group>
                    <Form.Label>  ,   .</Form.Label>
                    <Form.Control id={"criteriaDate"} type={"date"} placeholder=""
                                  onChange={onChange}/>
                </Form.Group>
                }
                <RightHorizontal/>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={close}>
                    
                </Button>
                <Button variant="primary" onClick={registerApi}>
                    
                </Button>
            </Modal.Footer>
        </Modal>
    )
}

export default VSettleRegisterForm;