import React from 'react';
import {Button, Form, Modal} from "react-bootstrap";

const VOrderStatusUpdate = ({show, close, onChange, onClick}) => {
    return (
        <Modal show={show} onHide={close}>
            <Modal.Header closeButton>
                <Modal.Title>  </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                  .
                <br/><br/>
                <Form.Group>
                    <Form.Label type="text"></Form.Label>
                    <Form.Control as="select" id="orderStatus" onChange={(event) => onChange(event)}>
                        <option value="ORDER"></option>
                        <option value="ORDER_CONFIRM"></option>
                        <option value="DELIVERY"></option>
                        <option value="DELIVERY_CONFIRM"></option>
                        <option value="CANCEL"></option>
                    </Form.Control>
                </Form.Group>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="primary" onClick={close}>
                    
                </Button>
                <Button variant="outline-success" onClick={onClick}>
                    
                </Button>
            </Modal.Footer>
        </Modal>

    )
}

export default VOrderStatusUpdate;