import React from 'react';
import {Button, Form} from "react-bootstrap";
import styled from "styled-components";
import {ButtonBox, RightHorizontal} from "../../../util/CommonStyledComponents";

const VRewardRegisterForm = ({
                                 singleRewardShow,
                                 validated,
                                 onSubmit,
                                 setShowOwnerModal,
                                 handleRewardChange,
                                 reward
                             }) => {
    return (
        <>
            {singleRewardShow && <>
                <br/>
                       .
                <br/><br/>
                <FormContainer>
                    <Form noValidate validated={validated} onSubmit={onSubmit}>
                        <Form.Row>
                            <Form.Group>
                                <Form.Label type="number">
                                    <Button style={{marginLeft: "90px", paddingTop: "0px", paddingBottom: "0px"}}
                                            size={"sm"}
                                            variant={"outline-info"}
                                            onClick={() => setShowOwnerModal(true)}></Button>
                                </Form.Label>
                                <Form.Control id={"ownerId"}
                                              onChange={handleRewardChange}
                                              value={reward.ownerId} required/>
                            </Form.Group>
                            <RightHorizontal/>
                            <Form.Group>
                                <Form.Label style={{width: "170px"}}> </Form.Label>
                                <Form.Control as="select" id="rewardType" onChange={handleRewardChange} required>
                                    <option value="DELIVERY_ACCIDENT"> </option>
                                    <option value="SYSTEM_ERROR"> </option>
                                    <option value="ABUSING"></option>
                                    <option value="ETC"></option>
                                </Form.Control>
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Group>
                                <Form.Label></Form.Label>
                                <Form.Control type={"dateTime-local"} id="rewardDateTime" onChange={handleRewardChange}
                                              required/>
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Group>
                                <Form.Label></Form.Label>
                                <Form.Control type={"number"} id="amount" onChange={handleRewardChange} required/>
                            </Form.Group>
                        </Form.Row>
                        <Form.Row>
                            <Form.Label></Form.Label>
                            <Form.Control id="description" as="textarea" rows={10} onChange={handleRewardChange}/>
                        </Form.Row>
                        <ButtonBox>
                            <Button type="submit" style={{borderRadius: "20px"}} variant={"outline-success"}>
                                </Button>
                        </ButtonBox>
                    </Form>
                </FormContainer>
            </>
            }
        </>
    )
}

export default VRewardRegisterForm;

const FormContainer = styled.div`
  display: flex;
  width: 100%;
  min-height: 600px;
`
