import React from 'react';
import {Button, Form} from "react-bootstrap";
import {ButtonBox, DefaultSearchBarLayout, RightHorizontal} from "../../../util/CommonStyledComponents";

const VRewardSearchCondition = ({search, setSearch, handleSearchChange, handleOwnerShow, fetchRewardApi}) => {
    return (
        <DefaultSearchBarLayout>
            <RightHorizontal/>
            <Form.Group>
                <Form.Label> 
                    <Button style={{marginLeft: "90px", paddingTop: "0px", paddingBottom: "0px"}}
                            size={"sm"} variant={"outline-info"}
                            onClick={handleOwnerShow}></Button>
                </Form.Label>
                <Form.Control id={"ownerName"} type={"text"} placeholder=""
                              onChange={(event) => handleSearchChange(event)}
                              value={search?.ownerName ?? ""}/>
            </Form.Group>
            <RightHorizontal/>

            <Form.Group>
                <Form.Label> </Form.Label>
                <Form.Control id={"rewardNo"} type={"text"} placeholder=""
                              onChange={(event) => handleSearchChange(event)}
                              value={search?.rewardNo ?? ""}
                />
            </Form.Group>
            <RightHorizontal/>

            <Form.Group>
                <Form.Label type="text"> </Form.Label>
                <Form.Control as="select" id="rewardType"
                              onChange={(event) => handleSearchChange(event)}
                              value={search?.rewardType ?? ""}
                >
                    <option value=""></option>
                    <option value="DELIVERY_ACCIDENT"> </option>
                    <option value="SYSTEM_ERROR"> </option>
                    <option value="ABUSING"></option>
                    <option value="ETC"></option>
                </Form.Control>
            </Form.Group>
            <RightHorizontal/>

            <ButtonBox>
                <Button variant="outline-danger" onClick={() => setSearch(null)}></Button>
            </ButtonBox>
            <RightHorizontal/>
            <ButtonBox>
                <Button variant="outline-success" onClick={() => fetchRewardApi()}></Button>
            </ButtonBox>
        </DefaultSearchBarLayout>
    )
}

export default VRewardSearchCondition;