export const API_BASE_URL = `http://localhost:8080`;
export const OAUTH2_REDIRECT_URI = `${process.env.REACT_APP_BACKEND_HOST}/oauth2/redirect`;
export const GOOGLE_AUTH_URL = `${API_BASE_URL}/oauth2/authorize/google?redirect_uri=${OAUTH2_REDIRECT_URI}`;

export const DATE_TIME_CONVERTER = (dateTime, onlyDate = false) => {
    const dateAndTime = dateTime.split("T");
    const date = dateAndTime[0].split("-");
    const time = dateAndTime[1].split(":");
    if (onlyDate) {
        return date[0] + " " + date[1] + " " + date[2] + " ";
    }
    return date[0] + " " + date[1] + " " + date[2] + " " + time[0] + " " + time[1] + " " + time[0] + "";
}

export const MONEY_DELIMITER = (input) => {
    return input.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") + "";
}

