import React from 'react';
import {Button, Form} from "react-bootstrap";
import {ButtonBox, DefaultSearchBarLayout, RightHorizontal} from "../../../util/CommonStyledComponents";

const VOwnerSearchCondition = ({search, onChange, init, onClick}) => {
    return (
        <DefaultSearchBarLayout>
            <Form.Group>
                <Form.Label> </Form.Label>
                <Form.Control id={"ownerId"} type={"number"} placeholder="."
                              value={search.ownerId}
                              onChange={onChange}/>
            </Form.Group>
            <RightHorizontal/>
            <Form.Group>
                <Form.Label> </Form.Label>
                <Form.Control id={"name"} type={"text"} placeholder="."
                              value={search.name}
                              onChange={onChange}/>
            </Form.Group>
            <RightHorizontal/>
            <Form.Group>
                <Form.Label> </Form.Label>
                <Form.Control id={"email"} type={"text"} placeholder="."
                              value={search.email}
                              onChange={onChange}/>
            </Form.Group>
            <RightHorizontal/>
            <Form.Group>
                <Form.Label> </Form.Label>
                <Form.Control id={"settleType"} as="select" placeholder=""
                              value={search?.settleType ?? ""}
                              onChange={(event) => onChange(event)}>
                    <option value=""></option>
                    <option value="DAILY"></option>
                    <option value="WEEK"></option>
                    <option value="MONTH"></option>
                </Form.Control>
            </Form.Group>
            <RightHorizontal/>
            <ButtonBox>
                <Button variant="outline-danger" onClick={init}>
                    
                </Button>
            </ButtonBox>
            <RightHorizontal/>
            <ButtonBox>
                <Button variant="outline-success" onClick={onClick}></Button>
            </ButtonBox>
        </DefaultSearchBarLayout>

    )
}

export default VOwnerSearchCondition;