import React from 'react';
import {Button, Form} from "react-bootstrap";
import {ButtonBox, DefaultSearchBarLayout, RightHorizontal} from "../../../util/CommonStyledComponents";

const VMemberSearchCondition = ({onChange, onClick}) => {
    return (
        <DefaultSearchBarLayout>
            <Form.Group>
                <Form.Label> </Form.Label>
                <Form.Control id={"id"} type={"number"} placeholder=""
                              onChange={onChange}/>
            </Form.Group>
            <RightHorizontal/>
            <Form.Group>
                <Form.Label> </Form.Label>
                <Form.Control id={"name"} type={"text"} placeholder=""
                              onChange={onChange}/>
            </Form.Group>
            <RightHorizontal/>

            <Form.Group>
                <Form.Label> </Form.Label>
                <Form.Control id={"email"} type={"text"} placeholder=""
                              onChange={onChange}/>
            </Form.Group>
            <RightHorizontal/>
            <ButtonBox>
                <Button variant="outline-success" onClick={() => onClick()}></Button>
            </ButtonBox>
        </DefaultSearchBarLayout>
    )
}

export default VMemberSearchCondition;